@use '../../scss/variables' as *;
@use '../../scss/mixins' as *;


.container{
    width: 100vw;
    height: 100vh;
    flex: 0 0 100vw;

    .imgContainer{
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            @include img;
        }
    }
    
}